<template>
    <div>
        <div class="reglog_header">
            <h3></h3>
            <i class="iconfont icon-back" @click="goBack"></i>
        </div>
        <div class="log_main">
            <div><input type="text" placeholder="" ref="userName" maxlength="11" @blur="alertFun"/></div>
            <div><input type="password" placeholder="" ref="pwd" maxlength="20" @blur="alertFun"/></div>
            <div class="logBtn">
                <!--<router-link :to="{name:'My'/*,query:{userName:userName}*/}"></router-link>-->
                <a @click="toFun" href="javascript:;"></a>
            </div>
            <div class="lastBox"><router-link class="meReg" :to="{name:'Register'}"></router-link><a class="findPwd"></a></div>
        </div>
        <div class="alertBox" ref="alertBox" v-show="alertState">{{alertText}}</div>
    </div>
</template>
<script>
import VueRouter from "vue-router";
var router = new VueRouter();
export default {
  name:"login",
  data(){
      return {
          alertText: null,//
          alertState:false//
      }
  },
  methods:{
      goBack(){
          history.go(-1);
      },
      alertFun(value) {
            // alert(this.$refs.userName.value);//
            var user = this.$refs.userName.value;
            var pwd = this.$refs.pwd.value;
            var that = this;
            if (!(/^1[34578]\d{9}$/.test(user))) {
                // this.$refs.alertBox.show().delay(3000).fadeOut();
                this.alertText = "";
                this.alertState = true;
                setTimeout(function(){
                    that.alertState = false;
                },2000)
            } else if (!(/^[A-Za-z0-9]{6,20}$/).test(pwd)) {
                // this.$refs.alertBox.show().delay(3000).fadeOut();
                this.alertState = true;
                this.alertText = "6-20";
                setTimeout(function(){
                    that.alertState = false;
                },2000)
            }
        },
        toFun(){
            var user = this.$refs.userName.value;
            var pwd = this.$refs.pwd.value;
            var that = this;
            if(!user==""&&!pwd==""){
                this.$http.get("http://bxu2359550159.my3w.com/wochuphp/login.php?username="+user+"&pwd="+pwd).then(function(res){
                    // console.log(res.body);
                    var t = res.body;
                    if(t == 0){
                        this.alertText = "";
                        this.alertState = true;
                        setTimeout(function(){
                            that.alertState = false;
                        },2000)
                    }else if(t==2){
                        this.alertText = "";
                        this.alertState = true;
                        setTimeout(function(){
                            that.alertState = false;
                        },2000)
                    }else {
                        // var userId = t.split("_")[1];
                        // console.log(userId);//id
                        this.alertState = true;
                        this.alertText = "";
                        setTimeout(function(){
                            that.alertState = false;
                            router.push({
                                // path:"/my",
                                // query:{userName:user}
                                path:that.$route.query.redirect
                            })
                        localStorage.setItem("userName",user);
                        },2000)
                    }
                    
                })
            }
            
        }
  }
}
</script>
