import Vue from 'vue'
import App from './App'
import router from './router'

import Resource from "vue-resource";//ajax jquery
Vue.use(Resource);

//vue
import verify from 'vue-verify-pop';// npm vue-verify-pop -S
Vue.use(verify);
// verify.errMsg = ""
// verify.addRule('myRule',(v)=>{return ''})

Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  template: '<App/>',
  components: { App }
})



